.movies {
  display: grid;
  grid-template-columns: 33% 33% 33%;

  /* gap: 10px; */
  justify-items: center;
  justify-content: center;
  width: 100%;
}

.movies__list {
  display: flex;
  justify-content: center;
  width: 100%;
}

.spin {
  display: flex;
  justify-content: center;
}

@media (max-width: 1200px) {
  .movies {
    display: grid;
    grid-template-columns: 50% 50%;

    justify-items: center;
    justify-content: center;
    width: 100%;
  }
}

@media (max-width: 900px) {
  .movies {
    display: grid;
    grid-template-columns: 100%;
    width: 100%;
  }
}
